                    {url:'http://www.kuwo.cn/',   images:'https://dss2.baidu.com/6ONYsjip0QIZ8tyhnq/it/u=2265480194,455370863&fm=58',title:'酷我音乐'},
                    {url:'https://www.xiami.com/',images:'https://dss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2655801445,825102614&fm=58',title:'虾米音乐'},
                    {url:'https://www.migu.cn/',  images:'https://dss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2326490028,614674444&fm=58',title:'咪咕音乐'},
                    {url:'http://www.9ku.com/',   images:'https://dss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2516311953,831350549&fm=58',title:'九酷音乐'},
