                ],
            }
        },
        created(){
            this. getMusicData()
        },
        methods:{

            getMusicData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1498701411149',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.MusicData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getMusicData(pageNum)
            
            },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

